.products__label{
    font-family: getFont('font-header');
    font-weight: getFontWeight('font-regular');
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    color: getColor("secondary-pink");
    margin-top: 121px;
}
.products__title{
    font-family: getFont('font-header');
    font-style: normal;
    font-weight: getFontWeight('font-regular');
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: getColor("accent");
    margin-top: 15px;
    margin-bottom: 144px;
}
.products__position{
    align-items: center;
    flex-direction: column;
    display: flex;
    position: relative; 
}
.products__list{
    display: block;
    align-items: center;
}
.products__item{
    position: relative;
    width: 280px;
    height: 485px;
    border-radius: 24px;
    margin-bottom: 130px;
}
.products__item--icecream{    
    background-color:getColor('primary-pink'); 
}
.products__item--icecoffee{
    background-color:getColor('primary-sand');
}
.products__item--milkshakes{
    background-color:getColor('primary-green');
}
.products__name{
    font-family: getFont('font-header');
    font-style: normal;
    font-weight: getFontWeight('font-regular');
    font-size: 20px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: getColor('background-white');
    position: absolute;
    top: 161px;
    left: 53px;
    right: 53px;
}
.products__description{
    font-family: getFont('font-main');
    width: 200px;
    font-style: normal;
    font-weight: getFontWeight('font-bold');
    font-size: 14px;
    line-height: 1.52;
    text-align: center;
    letter-spacing: 0.04em;
    color: getColor('background-white');       
    position: absolute;
    top: 250px;
    left: 39px;
    right: 41px;
}
.products__button{
    background-color: getColor("background-white");
    border: none;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display:flex;
    justify-content: center;
    cursor: pointer;
    position: absolute;
    top: 404px;
    left: 120px;
    right: 120px;
}
.products__icon{
    width: 15px;
    height: 15px;
    fill: getColor("accent");
    position: absolute;
    top: 12px;
}
.products__item--icecream::before{
    content: url('../images/products/pink/pink-mobile.png');
    display: block;
    position: absolute;
    top: -112px;
    left: 15px;
    right: 15px;
}
.products__item--icecoffee::before{
    content: url('../images/products/sand/sand-mobile.png');
    display: block;
    position: absolute;
    top: -112px;
    left: 15px;
    right: 15px;
}
.products__item--milkshakes::before{
    content: url('../images/products/green/green-mobile.png');
    display: block;
    position: absolute;
    top: -112px;
    left: 15px;
    right: 15px;
}
.products__name::after{
   /* content: '';
    background-image: url("../images/svg/smalldot.svg#icon-Group4");
    display: block;
    width: 6px;
    height: 6px;
    background-size: contain;
    left: 0;
    right: 0;
    margin: auto;
    position: absolute;
    */
        
    content: ('. . .');
    font-family: getFont(font-regular);
    font-weight:  getFontWeight('font-bold');
    font-size: 20px;
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 35px;
}

@media screen and (min-width: 768px) {
    .products__label{
        font-size: 26px;
        line-height: 30px;
        margin-top: 122px;
    }
    .products__title{
        font-size: 48px;
        line-height: 55px;
        margin-bottom: 150px;
    }
    .products__list{
        display: flex;
        flex-wrap: nowrap;
        gap: 20px;
    }
    .products__item{
        width: 220px;
        margin-bottom: 112px;
    }
    .products__name{
        width: 174px;
        left: 23px;
        right: 23px;
    }
    .products__description{
        width: 198px;
        line-height: 1.76;
        top: 241px;
        left: 11px;
        right: 11px;
    }
    .products__button{
        top: 430px;
        right: 90px;
        left: 90px;
    }
    .products__item--icecream::before{
        content: url('../images/products/pink/pink-tablet.png');
        top: -100px;
        left: 5px;
        right: 5px;
    }
    
    .products__item--icecoffee::before{
        content: url('../images/products/sand/sand-tablet.png');
        top: -100px;
        left: 5px;
        right: 5px;
    }
    .products__item--milkshakes::before{
        content: url('../images/products/green/green-tablet.png');
        top: -100px;
        left: 5px;
        right: 5px;
    }
}